import { ContactForm } from "@/components/contact/ContactForm"
import { Mail } from "lucide-react"

export const metadata = {
    title: "Contact Us | Powered Pipeline Pro",
    description: "Get in touch with our sales team or support.",
}

export default function ContactPage() {
    return (
        <div className="py-20">
            <div className="container">
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-16">
                    {/* Info Side */}
                    <div>
                        <h1 className="text-4xl font-bold mb-6">Get in touch</h1>
                        <p className="text-xl text-muted-foreground mb-12">
                            Have questions about our platform? We&apos;d love to hear from you.
                        </p>

                        <div className="space-y-8">
                            <div className="flex items-start space-x-4">
                                <div className="p-3 rounded-lg bg-primary/10">
                                    <Mail className="h-6 w-6 text-primary" />
                                </div>
                                <div>
                                    <h3 className="font-semibold text-lg mb-1">Email</h3>
                                    <p className="text-muted-foreground">Our friendly team is here to help.</p>
                                    <a href="mailto:sales@poweredpipepro.com" className="text-primary hover:underline mt-1 block">
                                        sales@poweredpipepro.com
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Form Side */}
                    <div className="bg-card p-8 rounded-2xl border">
                        <ContactForm />
                    </div>
                </div>
            </div>
        </div>
    )
}
